import { isNullOrUndefined } from './util';
export const validPhone = (phoneNumber) => /^\+?[1-9]\d{1,14}$/.test(phoneNumber);
export const validEmail = (email) => /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email);
export const validPassword = (password) => {
    return !(isNullOrUndefined(password)) && (password.length > 6);
};
export const validMongoID = (id = "") => {
    return /^[0-9a-fA-F]{24}$/.test(id);
};
